import React, { useEffect, useRef, useState } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CRow,
  CSpinner,
} from "@coreui/react";
import { useSelector } from "react-redux";
import axios from "axios";
import "./Order.css";
import { selectUser } from "src/localyStore";
import QRCode from "qrcode.react";
import ReactToPrint from "react-to-print";
const Order = ({ match }) => {
  const printAll = useRef(null);
  const invoice = useRef(null);
  const [orderData, setOrderData] = useState("");
  const [loding, setLoding] = useState(true);
  const [orderStatus, setOrederStatus] = useState({
    status: {
      Ordered: null,
      Packed: null,
      Shipped: null,
      Delivered: null,
    },
  });
  const [modal, setModal] = useState(false);
  const user = useSelector(selectUser);

  useEffect(() => {
    axios
      .get(
        process.env.REACT_APP_SERVER +
          process.env.REACT_APP_ADMIN_ORDER_GET_BY_ID +
          match.params.orderid,
        {
          headers: {
            Authorization: `Bearer ${user?.token}`,
          },
        }
      )
      .then((data) => {
        setOrderData(data.data.orders);
        setOrederStatus((prevState) => ({
          ...prevState,
          Ordered: data.data.orders.orderStatus[0].isCompleted,
          Cancelled: data.data.orders.orderStatus[1].isCompleted,
          Packed: data.data.orders.orderStatus[2].isCompleted,
          Shipped: data.data.orders.orderStatus[3].isCompleted,
          Delivered: data.data.orders.orderStatus[4].isCompleted,
        }));
        setLoding(false);
      })
      .catch((err) => {
        console.log(err);
        setLoding(false);
      });
  }, [modal, match.params.orderid, user?.token]);

  const toggle = () => {
    setModal(!modal);
  };
  const changeTrack = (e) => {
    if (e.target.classList[1] === "activeTrack") {
      e.target.classList.remove("activeTrack");
    } else {
      e.target.classList.add("activeTrack");
    }
  };
  const trackConfirm = () => {
    const order = document.querySelectorAll(".order_round");
    for (let i = 1; i < order.length; i++) {
      if (order[i].classList[1] === "activeTrack") {
        axios
          .post(
            process.env.REACT_APP_SERVER +
              process.env.REACT_APP_ADMIN_ORDER_UPDATE,
            {
              orderId: match.params.orderid,
              type: order[i].id,
            },
            {
              headers: {
                Authorization: `Bearer ${user?.token}`,
              },
            }
          )
          .then((res) => {
            console.log(res);
            toggle();
          })
          .catch((err) => {
            alert("error");
          });
      }
    }
  };

  return (
    <CRow>
      {!!loding && (
        <div
          style={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "100vh",
          }}
        >
          <CSpinner
            color="primary"
            style={{ width: "4rem", textAlign: "center", height: "4rem" }}
          />
        </div>
      )}
      {!loding && orderData && (
        <CCol lg={12}>
          <CCard>
            <CCardHeader>
              <div>
                <ReactToPrint
                  trigger={() => (
                    <CButton color="success" className="print_btn ml-4">
                      Print invoice
                    </CButton>
                  )}
                  content={() => invoice.current}
                />
                <ReactToPrint
                  trigger={() => (
                    <CButton color="success" className="print_btn">
                      Print
                    </CButton>
                  )}
                  content={() => printAll.current}
                />
              </div>
            </CCardHeader>
            <CButton lg={2} onClick={toggle} color="primary">
              Tracking Orders
            </CButton>
            <div ref={printAll}>
              <div className="d-flex justify-content-center my-5">
                <QRCode
                  value={match.params.orderid}
                  size={300}
                  bgColor={"#ffffff"}
                  fgColor={"#000000"}
                  level={"H"}
                  includeMargin={true}
                  renderAs={"svg"}
                  imageSettings={{
                    src: "data:image/png;base64,",
                    x: null,
                    y: null,
                    height: 30,
                    width: 30,
                    excavate: true,
                  }}
                />
              </div>
              <hr className="dotedBorderButton" />
              <CCardHeader className="center">
                Ultimodeal Online Shopping PVT. LTD.
              </CCardHeader>
              <CCardBody>
                <div className="d-flex justify-content-around">
                  <div>
                    <h4>Orders Details</h4>
                    <hr />
                    <p>Order id: {match.params.orderid}</p>
                    <p>Order Date: {orderData?.updatedAt}</p>
                    <p>PaymentType : {orderData?.paymentType} </p>
                    <p>OrginalPrice : {orderData?.orginalPrice}</p>
                    <p>Shipping Cost :{orderData?.shippingCharge} </p>
                    {orderData?.couponDiscount && (
                      <p>CouponDiscount :{orderData?.couponDiscount} </p>
                    )}
                    <p>Total Price :{orderData?.totalAmount} </p>
                    {orderData?.e_pay?.paymentName && (
                      <p>EPAY :{orderData?.e_pay?.paymentName} </p>
                    )}
                    {orderData?.e_pay?.token && (
                      <p>EPAY_TOKEN :{orderData?.e_pay?.token} </p>
                    )}
                    {orderData?.e_pay?.merchant?.name && (
                      <p>
                        EPAY_MERCHANT :{orderData?.e_pay?.merchant?.name},
                        {orderData?.e_pay?.merchant?.email}
                      </p>
                    )}

                    <p>CustomerEmail : {orderData?.userId?.email}</p>
                  </div>
                  <div>
                    <h4>Shipping Details</h4>
                    <hr />
                    <p>CustomerName : {orderData?.shipping?.name}</p>
                    <p>
                      CustomerPhoneNO : {orderData?.shipping?.mobileNumber},
                      {orderData.address?.alternatePhone}
                    </p>
                    <p>Province : {orderData?.shipping?.province},</p>
                    <p>Distrct : {orderData?.shipping?.district},</p>
                    <p>Area : {orderData?.shipping?.area}</p>
                    <p>Landmark : {orderData?.shipping?.landmark}</p>
                    <p>AddressType : {orderData?.shipping?.addressType}</p>
                  </div>
                  <div>
                    <h4>Billing Details</h4>
                    <hr />
                    <p>CustomerName : {orderData?.billing?.name}</p>
                    <p>
                      CustomerPhoneNO : {orderData?.billing?.mobileNumber},
                      {orderData.address?.alternatePhone}
                    </p>
                    <p>Province : {orderData?.billing?.province},</p>
                    <p>Distrct : {orderData?.billing?.district},</p>
                    <p>Area : {orderData?.billing?.area}</p>
                    <p>Landmark : {orderData?.billing?.landmark}</p>
                    <p>AddressType : {orderData?.billing?.addressType}</p>
                  </div>
                </div>
                <div className="m-5">
                  <h3>Products : </h3>
                  <hr />
                  <CRow>
                    <CCol style={{ width: "40px" }}></CCol>
                    <CCol>Name</CCol>
                    <CCol>attributes</CCol>
                    <CCol>quantity</CCol>
                    <CCol>price</CCol>
                  </CRow>
                  <hr />
                  {orderData?.products.map((i) => (
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-evenly",
                      }}
                    >
                      <CCol>
                        <img
                          width="40px"
                          src={i.productPictures?.[0]?.img}
                          alt={i.name}
                        />
                      </CCol>
                      <CCol>{i.name}</CCol>
                      <CCol>attributes</CCol>
                      <CCol>{i.quantity}</CCol>
                      <CCol>{i.price * i.quantity}</CCol>
                    </div>
                  ))}
                </div>
              </CCardBody>
            </div>
            {/* invoice is hidden and only use for print */}
            <div ref={invoice} className="invoice">
              <h2 className="my-4 underline">Tax Invoice</h2>
              <p>Purchase Price : {orderData?.orginalPrice}</p>
              <p>Shipping Cost : {orderData?.shippingCharge ?? "Free"}</p>
              <p>
                Coupon Discount : {orderData?.couponDiscount ?? "No Discount"}
              </p>
              <p>Tax 13% :{Math.ceil(orderData?.totalAmount * 0.13)}</p>
              <p>Total: {orderData?.totalAmount}</p>
            </div>
          </CCard>
        </CCol>
      )}
      {/* modal for order traking */}
      <CModal show={modal} onClose={toggle}>
        <CModalHeader closeButton>Order Tracking Update</CModalHeader>
        <CModalBody class="d-flex justify-content-around">
          <div className="order_tarcking_line"></div>
          <div className="order_tarcking">
            <p
              className={`order_round ${
                orderStatus.Ordered ? "activeTrack" : ""
              }`}
            ></p>
            Ordered
          </div>
          <div className="order_tarcking">
            <p
              onClick={changeTrack}
              id="Packed"
              className={`order_round ${
                orderStatus.Packed ? "activeTrack" : ""
              }`}
            ></p>
            Packing
          </div>
          <div className="order_tarcking">
            <p
              onClick={changeTrack}
              id="Shipped"
              c
              className={`order_round ${
                orderStatus.Shipped ? "activeTrack" : ""
              }`}
            ></p>
            Shipping
          </div>
          <div className="order_tarcking">
            <p
              onClick={changeTrack}
              id="Delivered"
              className={`order_round ${
                orderStatus.Delivered ? "activeTrack" : ""
              }`}
            ></p>
            Deliver
          </div>
        </CModalBody>
        <CModalFooter>
          <CButton onClick={trackConfirm} color="primary">
            Confirm
          </CButton>
        </CModalFooter>
      </CModal>
    </CRow>
  );
};

export default Order;
